#sidebar-wrapper {
  z-index: 1000;
  position: fixed;
  left: 180px;
  width: 0;
  height: 100%;
  margin-left: -180px;
  overflow-y: auto;
  overflow-x: hidden;
  /* background: #fff; */
  /* background-color: rgb(34,34,34); */
  background-color: #383838;
  -webkit-transition: all 0.5s ease;
  transition: all 0.5s ease;
  box-shadow: 0px 0px 3px 4px #1f1f1f;
}

#sidebar-close {
  display: none;
}

#wrapper.toggled #sidebar-wrapper {
  width: 180px;
}

#home,
#close {
  padding-right: 20px;
}

.sidebar-brand {
  position: absolute;
  top: 0;
  width: 180px;
  text-align: left;
  padding: 20px 0 0 20px;
}
.sidebar-brand,
#sidebar-close h2 {
  margin: 0;
  font-weight: 600;
  font-size: 18px;
  color: #fff;
}

.sidebar-nav {
  position: absolute;
  top: 105px;
  width: 180px;
  margin: 0;
  padding: 0;
  list-style: none;
  display: block;
}
.sidebar-nav > li {
  text-indent: 10px;
  line-height: 42px;
}
.sidebar-nav > li a {
  display: block;
  text-decoration: none;
  color: #757575;
  /* color: #0c7664; */
  font-weight: 600;
  font-size: 18px;
}
.sidebar-nav > li > a:hover,
.sidebar-nav > li.active > a {
  text-decoration: none;
  color: #fff;
  /* background: #f8be12; */
  /* color: #000000; */
  /* background: #0c7664;  */
  background: #757575;
}

.active-nav {
  text-decoration: none;
  color: #fff;;
  /* background: #f8be12; */
  /* background: #a9d8cc; */
  /* background: #0c7664;  */
  background: #757575;
}

li.active-nav a {
  text-decoration: none;
  color: #fff;;
  /* background: #f8be12; */
  /* background: #a9d8cc; */
  /* background: #0c7664;  */
  background: #757575
}

.sidebar-nav > li > a i.fa {
  font-size: 24px;
  width: 60px;
}

#sidebar-toggle,
#header {
  display: none;
}

